"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { ChevronLeft, ChevronRight, Sparkles } from "lucide-react"

const slides = [
  {
    id: 1,
    title: "Barati Safa & Wedding Turbans",
    subtitle: "Celebrate in Style",
    description: "Premium traditional safas for baraat ceremonies, perfect for groom's entourage",
    image: "/1.png",
  },
  {
    id: 2,
    title: "Destination Wedding Turbans",
    subtitle: "Elegance Across Borders",
    description: "Complete turban solutions for your dream wedding at any exotic location",
    image: "/2.png",
  },
  {
    id: 3,
    title: "Groom Safa & Pagdi",
    subtitle: "Your Perfect Royal Look",
    description: "Exquisite handcrafted safas designed exclusively for the groom's grand entrance",
    image: "/3.png",
  },
]

export function HeroSlider() {
  const [currentSlide, setCurrentSlide] = useState(0)

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % slides.length)
    }, 5000)
    return () => clearInterval(timer)
  }, [])

  const nextSlide = () => {
    setCurrentSlide((prev) => (prev + 1) % slides.length)
  }

  const prevSlide = () => {
    setCurrentSlide((prev) => (prev - 1 + slides.length) % slides.length)
  }

  return (
    <section id="home" className="relative h-screen md:h-screen sm:h-[500px] xs:h-[400px] overflow-hidden">
      {slides.map((slide, index) => (
        <div
          key={slide.id}
          className={`absolute inset-0 transition-opacity duration-1000 ${
            index === currentSlide ? "opacity-100" : "opacity-0"
          }`}
        >
          <div className="absolute inset-0 bg-gradient-to-r from-primary/90 via-primary/50 to-transparent z-10" />
          <img src={slide.image || "/placeholder.svg"} alt={slide.title} className="w-full h-full object-cover" />
          <div className="absolute inset-0 z-20 flex items-center">
            <div className="container mx-auto px-4">
              <div className="max-w-3xl space-y-4 md:space-y-6">
                <div className="flex items-center gap-2 text-accent">
                  <Sparkles className="w-4 h-4 md:w-5 md:h-5" />
                  <span className="text-xs md:text-sm font-mono uppercase tracking-wider">{slide.subtitle}</span>
                </div>
                <h2 className="text-3xl sm:text-4xl md:text-7xl font-bold text-primary-foreground leading-tight text-balance">
                  {slide.title}
                </h2>
                <p className="text-base sm:text-lg md:text-2xl text-primary-foreground/90 font-mono leading-relaxed">
                  {slide.description}
                </p>
                <div className="flex flex-col sm:flex-row gap-3 md:gap-4 pt-2 md:pt-4">
                  <Button
                    size="lg"
                    className="bg-accent hover:bg-secondary text-accent-foreground text-base md:text-lg"
                    onClick={() => {
                      const element = document.getElementById("contact")
                      element?.scrollIntoView({ behavior: "smooth" })
                    }}
                  >
                    Book Now
                  </Button>
                  <Button
                    size="lg"
                    variant="outline"
                    className="bg-primary-foreground/10 hover:bg-primary-foreground/20 text-primary-foreground border-primary-foreground/30 backdrop-blur-sm text-base md:text-lg"
                    onClick={() => {
                      const element = document.getElementById("packages")
                      element?.scrollIntoView({ behavior: "smooth" })
                    }}
                  >
                    View Packages
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
      ))}

      {/* Navigation Arrows */}
      <button
        onClick={prevSlide}
        className="absolute left-4 top-1/2 -translate-y-1/2 z-30 bg-primary-foreground/20 hover:bg-primary-foreground/30 backdrop-blur-sm text-primary-foreground p-3 rounded-full transition-all"
      >
        <ChevronLeft className="w-6 h-6" />
      </button>
      <button
        onClick={nextSlide}
        className="absolute right-4 top-1/2 -translate-y-1/2 z-30 bg-primary-foreground/20 hover:bg-primary-foreground/30 backdrop-blur-sm text-primary-foreground p-3 rounded-full transition-all"
      >
        <ChevronRight className="w-6 h-6" />
      </button>

      {/* Dots Indicator */}
      <div className="absolute bottom-8 left-1/2 -translate-x-1/2 z-30 flex gap-2">
        {slides.map((_, index) => (
          <button
            key={index}
            onClick={() => setCurrentSlide(index)}
            className={`h-2 rounded-full transition-all ${
              index === currentSlide ? "w-8 bg-accent" : "w-2 bg-primary-foreground/50 hover:bg-primary-foreground/75"
            }`}
          />
        ))}
      </div>
    </section>
  )
}
